<script>
import { uploadImgFile } from "../../assets/scripts/uploadImageFile";

export default {
    props: {
        value: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            formGitHub: {
                repo: "",
                branch: "",
                accessToken: "",
            },
            formGitee: {
                repo: "",
                branch: "",
                accessToken: "",
            },
            formAliOSS: {
                accessKeyId: "",
                accessKeySecret: "",
                bucket: "",
                region: "",
                path: "",
                cdnHost: "",
            },
            formTxCOS: {
                secretId: "",
                secretKey: "",
                bucket: "",
                region: "",
                path: "",
                cdnHost: "",
            },
            formQiniu: {
                accessKey: "",
                secretKey: "",
                bucket: "",
                domain: "",
                region: "",
            },
            options: [
                {
                    value: "default",
                    label: "",
                },
                {
                    value: "gitee",
                    label: "Gitee",
                },
                {
                    value: "github",
                    label: "GitHub",
                },
                {
                    value: "aliOSS",
                    label: "",
                },
                {
                    value: "txCOS",
                    label: "",
                },
                {
                    value: "qiniu",
                    label: "",
                },
            ],
            imgHost: "default",
            uploadingImg: false,
        };
    },
    created() {
        if (localStorage.getItem("githubConfig")) {
            this.formGitHub = JSON.parse(localStorage.getItem("githubConfig"));
        }
        if (localStorage.getItem("giteeConfig")) {
            this.formGitee = JSON.parse(localStorage.getItem("giteeConfig"));
        }
        if (localStorage.getItem("aliOSSConfig")) {
            this.formAliOSS = JSON.parse(localStorage.getItem("aliOSSConfig"));
        }
        if (localStorage.getItem("txCOSConfig")) {
            this.formTxCOS = JSON.parse(localStorage.getItem("txCOSConfig"));
        }
        if (localStorage.getItem("imgHost")) {
            this.imgHost = localStorage.getItem("imgHost");
        }
    },
    methods: {
        changeImgHost() {
            localStorage.setItem("imgHost", this.imgHost);
            this.$message({
                showClose: true,
                message: "",
                type: "success",
            });
        },
        saveGitHubConfiguration() {
            if (!(this.formGitHub.repo && this.formGitHub.accessToken)) {
                const blankElement = this.formGitHub.repo
                    ? "token"
                    : "GitHub ";
                this.$message({
                    showClose: true,
                    message: `${blankElement}`,
                    type: "error",
                });
                return;
            }
            localStorage.setItem(
                "githubConfig",
                JSON.stringify(this.formGitHub)
            );
            this.$message({
                message: "",
                type: "success",
            });
        },
        saveGiteeConfiguration() {
            if (!(this.formGitee.repo && this.formGitee.accessToken)) {
                const blankElement = this.formGitee.repo
                    ? ""
                    : "Gitee ";
                this.$message({
                    showClose: true,
                    message: `${blankElement}`,
                    type: "error",
                });
                return;
            }
            localStorage.setItem("giteeConfig", JSON.stringify(this.formGitee));
            this.$message({
                message: "",
                type: "success",
            });
        },
        saveAliOSSConfiguration() {
            if (
                !(
                    this.formAliOSS.accessKeyId &&
                    this.formAliOSS.accessKeySecret &&
                    this.formAliOSS.bucket &&
                    this.formAliOSS.region
                )
            ) {
                this.$message({
                    showClose: true,
                    message: ` OSS `,
                    type: "error",
                });
                return;
            }
            localStorage.setItem(
                "aliOSSConfig",
                JSON.stringify(this.formAliOSS)
            );
            this.$message({
                message: "",
                type: "success",
            });
        },

        saveTxCOSConfiguration() {
            if (
                !(
                    this.formTxCOS.secretId &&
                    this.formTxCOS.secretKey &&
                    this.formTxCOS.bucket &&
                    this.formTxCOS.region
                )
            ) {
                this.$message({
                    showClose: true,
                    message: ` COS `,
                    type: "error",
                });
                return;
            }
            localStorage.setItem("txCOSConfig", JSON.stringify(this.formTxCOS));
            this.$message({
                message: "",
                type: "success",
            });
        },

        saveQiniuConfiguration() {
            if (
                !(
                    this.formQiniu.accessKey &&
                    this.formQiniu.secretKey &&
                    this.formQiniu.bucket &&
                    this.formQiniu.domain &&
                    this.formQiniu.region
                )
            ) {
                this.$message({
                    showClose: true,
                    message: ` Kodo `,
                    type: "error",
                });
                return;
            }
            localStorage.setItem("qiniuConfig", JSON.stringify(this.formQiniu));
            this.$message({
                message: "",
                type: "success",
            });
        },

        // 
        beforeUpload(file) {
            if (!this.validateConfig()) {
                return;
            }

            this.uploadingImg = true;
            uploadImgFile(file)
                .then((res) => {
                    this.$emit("uploaded", res);
                    this.uploadingImg = false;
                })
                .catch((err) => {
                    this.uploadingImg = false;
                    this.$message({
                        showClose: true,
                        message: err,
                        type: "error",
                    });
                });
            return false;
        },
        validateConfig() {
            let checkRes = true,
                errMessage = "";

            switch (localStorage.getItem("imgHost")) {
                case "github":
                    checkRes =
                        this.formGitHub.repo && this.formGitHub.accessToken;
                    errMessage = checkRes ? "" : " GitHub ";
                    break;
                case "gitee":
                    checkRes =
                        this.formGitee.repo && this.formGitee.accessToken;
                    errMessage = checkRes ? "" : " Gitee ";
                    break;
                case "aliOSS":
                    checkRes =
                        this.formAliOSS.accessKeyId &&
                        this.formAliOSS.accessKeySecret &&
                        this.formAliOSS.bucket &&
                        this.formAliOSS.region;
                    errMessage = checkRes ? "" : " OSS ";
                    break;
                case "txCOS":
                    checkRes =
                        this.formTxCOS.secretId &&
                        this.formTxCOS.secretKey &&
                        this.formTxCOS.bucket &&
                        this.formTxCOS.region;
                    errMessage = checkRes ? "" : " COS ";
                    break;
                case "qiniu":
                    checkRes =
                        this.formQiniu.accessKey &&
                        this.formQiniu.secretKey &&
                        this.formQiniu.bucket &&
                        this.formQiniu.domain &&
                        this.formQiniu.region;
                    errMessage = checkRes ? "" : " Kodo ";
                    break;
            }
            errMessage && this.$message.error(errMessage);
            return checkRes;
        },
    },
};
</script>

